*{
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}
.hero{
    height: 100vh;
    background-image: linear-gradient(rgba(0, 0, 0, 0.8),rgba(0,0,0,0.6)),
    url(./images/backgorund.jpg);
    background-position: center;
    background-size: cover;
    overflow: hidden;
    position: relative;
}
.nav-bar{
    display: flex;
    padding: 40px 120px;
    
}
.nav-logo img{
width: 130px;
}
.nav-links{
    flex: 1;
    
}
.nav-links ul{
    margin-left: 50px;
    display: inline;
   
}
.nav-links ul li{
    list-style: none;
    display: inline-block;
    padding: 8px 25px;
   
}
.nav-links ul a{
    color: white;
    text-decoration: none;
    font-size: 13px;
}


.btn{

padding: 10px 20px;
border: 0;
background: yellow;
font-weight: bold;
cursor: pointer;

}
.nav-links .btn {
    float: right;
}
.banner-title{
    margin: 80px 130px;
    color: #fff;
}
.banner-title h1{
    font-size: 64px;
    margin-bottom: 30px;
}
.banner-title h1 span{
    color: yellow;
}
.vertical-bar{
    height: 100%;
    background: #000;
    top: 0;
    left: 0;
    position: absolute;
    vertical-align: middle;
}
.list-icon{
    height: 100%;
    width: 100px;
    background: yellow;
    text-align: center;
    padding-bottom: 100%;
    padding-top: 100%;
    position: flex;
  
    
}

.search-icon.fa {
   
    display: block;
    color: #fff;
    font-size: 30px;
    cursor: pointer;
    background: yellow;
    

 
    
}
.list-icon.fa {
  
    display: block;
    color: #fff;
    font-size: 30px;
    cursor: pointer;
    background: yellow;
   
}
.social-icons{
    height: 35%;
    width: 80px;
    background: yellow;
    text-align: center;
    padding-top: 27%;
    bottom: 0;
    position: absolute;
}
.social-icons .fa{
    margin: 15px 23px;
    display: block;
    padding: 8px;
    border: 1px solid #000;
    border-radius: 50%;
    cursor: pointer;
}
.notifications{
    display: flex;
    width: 400px;
    right: 0;
    bottom: 0;
    position: absolute;
    background: yellow;
    align-items: center;
}
.contents{
    
  padding: 10PX;
 
}
.notification-img{
  
   text-decoration: none;

}
.notifications a{
    
    text-decoration: none;
}
.notification-img img{
    width: 100%;
   
}
.contents a{
    text-decoration: none;
    color: #000;
    font-size: 13px;
    margin: 0 10px;
}
.contents p{
    
    margin-top: 10px;
}
.small{
    text-align: center;
}
@media ( max-width:700px) {
    .vertical-bar{
        display: none;

    }
    .banner-title{
        margin: 80px 40px;
    }
    .banner-title h1{
        font-size: 50px;
    }
    .notifications{
        width: 100%;
    }
    .nav-bar{
        padding: 10px 30px;
    }
    .fa-bars{
        position: absolute;
        right: 20px;
        top: 10px;
    }
    .nav-bar .fa{
        display: block;
        color: #fff;
        margin: 10 px 25px;
        font-size: 22px;
        cursor: pointer;
    }
    .nav-links{
        height: 100vh;
        width: 200px;
        background: #000;
        top: 0;
        right: -200px;
        position: absolute;
        text-align: left;
        z-index: 2;
    }
    .nav-links ul a{
        display: block;
    }
    .nav-links .btn{
        float: none;
        margin-left: 25px;
        margin-top: 10px;
    }
}